
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import Header from '../components/Header.vue'
import Footer from '../components/Footer.vue'
import NoData from '../components/NoData.vue'
import axios from 'axios'
import { mapGetters } from 'vuex'

export default {
name: 'Home',
directives: {
TransferDom
},
components: {
Header,
Footer,
Tab,
TabItem,
Swiper,
SwiperItem,
XDialog,
XSwitch,
Popup,
Group,
Flexbox,
FlexboxItem,
NoData,
},
data () {
return {
showHideOnBlur: false,
showNewsDetails: false,
newDetails: {},
selected: "推荐", // 默认选中项
index: 0,
myNews: [],
pushNews: [],
newList: [],
}
},
computed:{
...mapGetters([
'newsList',
])
},
created: function() {
this.myNews = this.newsList.slice(0,9);
this.pushNews = this.newsList.slice(9);
axios.get('https://www.easy-mock.com/mock/5d0b684896278014af027705/TodayHeadlines/newsList')
.then( res => {
this.newList = res.data.data.list || [e];
})
.catch(function (error) {
console.log(error);
});
},
methods: {

moreNews () {
this.showHideOnBlur = !this.showHideOnBlur;
},
menuSelect(data) {
this.selected = data.title;
scrollTo(0, 0);
axios.get(`https://www.easy-mock.com/mock/5d0b684896278014af027705/TodayHeadlines/newsList?id=${data.id}`)
.then((res) => {
console.log(res)
console.log(res.data.data)
this.newList = res.data.data.list || [e];
console.log(this.newList)
})
.catch(function (error) {
console.log(error);
});
},

deletNews (item) {
let index = this.myNews.indexOf(item);
this.myNews.splice(index,1);
this.pushNews.unshift(item);
this.selected = this.myNews[0].title;
},

addNews (item) {
let index = this.myNews.indexOf(item);
this.pushNews.splice(index,1);
this.myNews.unshift(item);
this.selected = this.myNews[0].title;
},
toNewsDetails (data) {
this.newDetails = data;
this.showNewsDetails = true;
}
},
}



